.adminContainer {
  padding: 0 24px;
  margin-top: 45px;
}

.adminLogo {
  display: flex;
  align-items: center;
  justify-content: center;
}

.adminTitle {
  color: #000000;
  padding: 0 24px 0 24px;
  margin-top: 29px;
  text-align: center;
}

.adminInputGroup {
  margin: 16px 0 24px 0;
}

.adminButtonGroup {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin: 22px 10px;
}

.adminButtonSecGroup {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.adminRegisterButton,
.forntButton {
  color: #0062ff;
}

@media screen and (min-width: 991px) {
  .adminContainer {
    width: 356px;
    padding: 0 24px;
    margin: 45px auto;
  }
}

@media screen and (min-width: 1200px) {
  .adminContainer {
    max-width: 1140px;
    padding: 0 24px;
    margin: 45px auto;
  }
}