.LayoutMainContainer {
  height: 100vh;
}

.LayoutPopularContainer {
  display: none;
}

.modalPostTweet {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: #fff;
  z-index: 3;
}

@media screen and (min-width:992px){
  .LayoutContainer {
    display: flex;
  }

.LayoutSidebarContainer {

  padding-left: 23px;
}

.LayoutMainContainer {
  flex: 1;
  margin: 0 25px;
  border: 1px solid #E6ECF0;
}

.LayoutPopularContainer {
  display: block;
  width: 273px;
  padding-top: 16px;
  margin-right: 23px;
}
.modalPostTweet {
    height: 300px;
    width: 634px;
    border-radius: 14px;
    top: 56px;
    left: 50%;
    transform: translateX(-50%);
  }
  .modalBackground {
    position: absolute;
    top: 0;
    left: 0;
      height: 100vh;
      width: 100vw;
      background-color: #787878 ;
      opacity: .7;
    } 
}

@media screen and (min-width:1200px){
  .LayoutSidebarContainer {
  margin-left: 138px;
}
  .LayoutPopularContainer {
  margin-right: 138px;
}
}
