@use '../../styles/variable.module' as var;

.postTweetContainer {
  display: flex;
  flex-direction: column;
  height: calc(100% - var.$sidebar-height);
  width: 100%;
}

.avatar{
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.postTweetForm {
  height: 100%;

}

.postTweetContent {
  height: 100%;
  padding:16px 24px;
}

.contentGroup {
  display: flex;
  gap: 8px;
  height: 100%;

  .postTweetInputGroup {
    height: 100%;
    width: 100%;
    position: relative;
  }
  

  .postTweetInput {
    resize: none;
    border: none;
    box-shadow: none;
    outline: none;
    color: #6c757d;
    font-size: 18px;
    font-weight: 700;
    width: 100%;
    height: 100%;
    overflow: scroll;
    margin-top: 5px;
  }

  .footerText {
    position: absolute;
    bottom: 0;
    right: 0;
    text-align: right;
    color: #FC5A5A;
  }
  
}

