@use '../../styles/variable.module' as var;

.sideBarWrapper {
  width: 100%;
  position: fixed;
  bottom: 0;
  border-top: 1px solid #E6ECF0;;
  background-color: #fff;
  z-index: 1;
  height: var.$sidebar-height;
}

.iconGroup {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 10px 0;
}

.logo{
  display: none;
}
.buttonItem{
  display: none;
}

@media screen and (min-width:992px) {
  .sideBarWrapper {
    height: 100vh;
    background-color: unset;
    position: static;

    .sideBarContainer {
      height: 100%;

    }

    .sideBarGroup {
      height: 100%;
      
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin: 8px 0;

    }

    .iconGroup {
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      gap: 45px;
 
    }
    .sideBarItems {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 25px;
    }
    .logo {
      display: block;
    }
    .loginOut {
      margin: 17px 0;
    }
  }
}

@media screen and (min-width:1200px) {
  .sideBarWrapper {

    .iconGroup {
      display: flex;
      align-items: start;
      justify-content: center;
      padding: 0 15px;
      gap: 25px;
    } 
  }
  .sideBarContainer {
      height: 100%;

      margin: 8px auto;
    }
  .sideBarItems {
    display: flex;
    align-items: start;
    width: 100%;

    .logo {
      width: 100%;
      display: flex;
      justify-content: start;
      padding: 0 15px;
    }
  }

  .buttonItem {
    display: block;
    width: 100%;
  }
  .loginOut {
    display: flex;
    padding: 0 15px;
  }

  .sideBarGroup {
    width: 178px;
  }

}