.tweetsContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
  padding-bottom: 52px;
}

.mainTweets {
  height: 100vh;
  overflow-y: scroll;
  flex: 1;
}

.tweetsHeader {
  border-bottom: 1px solid #E6ECF0; 
}
.tweetsNavbar {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 24px;
  margin: 12px 24px 13px;
  
}

.navAvatar{
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.mainTweetsAvatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.tweetPostArea {
  display: none;
}

@media screen and (min-width:992px){
  .tweetsContainer {
    padding-bottom: unset;
  }

  .tweetPostArea {
  display: block;
  border-bottom: 10px solid #E6ECF0;
  height: 130px;
} 
}